<template>
  <ul class="stack">
    <li
      v-for="(item, index) in pages"
      :key="item.id"
      class="stack-item"
      :style="[transformIndex(index),transform(index)]"
      @touchmove.stop.capture="touchmove"
      @touchstart.stop.capture="touchstart"
      @touchend.stop.capture="touched"
      @mousedown.stop.capture="touchstart"
      @mouseup.stop.capture="touchend"
      @mousemove.stop.capture="touchmove"
      @webkit-transition-end="onTransitionEnd"
      @transitionend="onTransitionEnd"
    >
      <img :src="item.src">
    </li>
  </ul>
</template>

<script>
// Êé¢Êé¢ÊªëÂä®ÊïàÊûúÂç°Áâá
export default {
  props: {
    pages: {
      type: Array,
      default: () => [] // ÈªòËÆ§ÂÄºÂøÖÈ°ª‰∏∫‰∏Ä‰∏™ÂáΩÊï∞ÁöÑËøîÂõûÂÄº
    }
  },
  data() {
    return {
      // basicdataÊï∞ÊçÆÂåÖÂê´ÁªÑ‰ª∂Âü∫Êú¨Êï∞ÊçÆ
      basicdata: {
        start: {}, // ËÆ∞ÂΩïËµ∑Âßã‰ΩçÁΩÆ
        end: {}, // ËÆ∞ÂΩïÁªàÁÇπ‰ΩçÁΩÆ
        currentPage: 0 // ÈªòËÆ§È¶ñÂõæÁöÑÂ∫èÂàó
      },
      // temporaryDataÊï∞ÊçÆÂåÖÂê´ÁªÑ‰ª∂‰∏¥Êó∂Êï∞ÊçÆ
      temporaryData: {
        posWidth: '', // ËÆ∞ÂΩï‰ΩçÁßª
        posHeight: '', // ËÆ∞ÂΩï‰ΩçÁßª
        lastPosWidth: '', // ËÆ∞ÂΩï‰∏äÊ¨°ÊúÄÁªà‰ΩçÁßª
        lastPosHeight: '', // ËÆ∞ÂΩï‰∏äÊ¨°ÊúÄÁªà‰ΩçÁßª
        tracking: false, // ÊòØÂê¶Âú®ÊªëÂä®ÔºåÈò≤Ê≠¢Â§öÊ¨°Êìç‰ΩúÔºåÂΩ±Âìç‰ΩìÈ™å
        animation: false, // È¶ñÂõæÊòØÂê¶ÂêØÁî®Âä®ÁîªÊïàÊûúÔºåÈªòËÆ§‰∏çÂêØÁî®
        opacity: 1, // ËÆ∞ÂΩïÈ¶ñÂõæÁöÑÈÄèÊòéÂ∫¶
        swipe: false // onTransitionÂà§ÂÆöÊù°‰ª∂
      }
    }
  },
  methods: {
    touchstart(e) {
      console.log(e)
      if (this.temporaryData.tracking) {
        return
      }
      // ÊòØÂê¶‰∏∫touch
      if (e.type === 'touchstart') {
        if (e.touches.length > 1) {
          this.temporaryData.tracking = false
          return
        } else {
          // ËÆ∞ÂΩïËµ∑Âßã‰ΩçÁΩÆ
          this.basicdata.start.t = new Date().getTime()
          this.basicdata.start.x = e.targrtTouches[0].clientX
          this.basicdata.start.y = e.targrtTouches[0].clientY
          this.basicdata.end.x = e.targrtTouches[0].clientX
          this.basicdata.end.y = e.targrtTouches[0].clientY
        }
        // PCÊìç‰Ωú
      } else {
        this.basicdata.start.t = new Date().getTime()
        this.basicdata.start.x = e.clientX
        this.basicdata.start.y = e.clientY
        this.basicdata.end.x = e.clientX
        this.basicdata.end.y = e.clientY
      }
      this.temporaryData.tracking = true
      this.temporaryData.animation = false
    },
    touchmove(e) {
      // ËÆ∞ÂΩïÊªëÂä®‰ΩçÁΩÆ
      if (this.temporaryData.tracking && !this.temporaryData.animation) {
        if (e.type === 'touchmove') {
          this.basicdata.end.x = e.targrtTouches[0].clientX
          this.basicdata.end.y = e.targrtTouches[0].clientY
        } else {
          this.basicdata.end.x = e.clientX
          this.basicdata.end.y = e.clientY
        }
        // ËÆ°ÁÆóÊªëÂä®ÂÄº
        this.temporaryData.posWidth = this.basicdata.end.x - this.basicdata.start.x
        this.temporaryData.posHeight = this.basicdata.end.y - this.basicdata.start.y
      }
    },
    touchend(e) {
      this.temporaryData.tracking = false
      this.temporaryData.animation = true
      // ÊªëÂä®ÁªìÊùüÔºåËß¶ÂèëÂà§Êñ≠
      // ÁÆÄÂçïÂà§Êñ≠ÊªëÊ°£ÂÆΩÂ∫¶Ë∂ÖÂá∫100pxÊó∂Ëß¶ÂèëÊªëÂä®
      if (Math.abs(this.temporaryData.posWidth) >= 100) {
        // ÊúÄÁªà‰ΩçÁßªÁÆÄÂçïËÆæÂÆö‰∏∫xËΩ¥200pxÁöÑÂÅèÁßª
        const ratio = Math.abs(
          this.temporaryData.posHeight / this.temporaryData.posWidth
        )
        this.temporaryData.posWidth = this.temporaryData.posWidth >= 0 ? this.temporaryData.posWidth + 200 : this.temporaryData.posWidth - 200
        this.temporaryData.posHeight = this.temporaryData.posHeight >= 0 ? Math.abs(this.temporaryData.posWidth * ratio) : -Math.abs(this.temporaryData.posWidth * ratio)
        this.temporaryData.opacity = 0
        this.temporaryData.swipe = true
        // ËÆ∞ÂΩïÊúÄÁªàÊªëÂä®Ë∑ùÁ¶ª
        this.temporaryData.lastPosWidth = this.temporaryData.posWidth
        this.temporaryData.lastPosHeight = this.temporaryData.posHeight
        // currentPage+1 ÂºïÂèëÊéíÂ∫èÂèòÂåñ
        this.basicdata.currentPage += 1
        // currentPageÂàáÊç¢ÔºåÊï¥‰ΩìdomËøõË°åÂèòÂåñÔºåÊääÁ¨¨‰∏ÄÂ±ÇÊªëÂä®ÁΩÆÈõ∂
        this.$nextTick(() => {
          this.temporaryData.posWidth = 0
          this.temporaryData.posHeight = 0
          this.temporaryData.opacity = 1
        })
        // ‰∏çÊª°Ë∂≥Êù°‰ª∂ÂàôÊªëÂõûÂéü‰Ωç
      } else {
        this.temporaryData.posWidth = 0
        this.temporaryData.posHeight = 0
        this.temporaryData.swipe = false
      }
    },
    onTransitionEnd(index) {
      // domÂèëÁîüÂèòÂåñÂêéÔºåÊ≠£Âú®ÊâßË°åÁöÑÂä®ÁîªÊªëÂä®Â∫èÂàóÂ∑≤ÁªèÂèò‰∏∫‰∏ä‰∏ÄÂ±Ç
      if (
        this.temporaryData.swipe &&
                index === this.basicdata.currentPage - 1
      ) {
        this.temporaryData.animation = true
        this.temporaryData.lastPosWidth = 0
        this.temporaryData.lastPosHeight = 0
        this.temporaryData.swipe = false
      }
    },
    // ÈùûÈ¶ñÈ°µÊ†∑ÂºèÂàáÊç¢
    transform(index) {
      if (index > this.basicdata.currentPage) {
        const style = {}
        const visible = 3
        const perIndex = index - this.basicdata.currentPage
        // visibleÂèØËßÅÊï∞ÈáèÂâçÊªëÂùóÁöÑÊ†∑Âºè
        if (index <= this.basicdata.currentPage + visible - 1) {
          style['opacity'] = '1'
          style['transform'] = `translate3D(0,0,${-1 *
                        perIndex *
                        60}px)`
          style['zIndex'] =
                        visible - index + this.basicdata.currentPage
          style['transitionTimingFunction'] = 'ease'
          style['transitionDuration'] = 300 + 'ms'
        } else {
          style['zIndex'] = '-1'
          style['transform'] = `translated3D(0,0,${-1 *
                        visible *
                        60}px)`
        }
        return style
        // Â∑≤ÊªëÂä®Ê®°ÂùóÈáäÊîæÂêé
      } else if (index === this.basicdata.currentPage - 1) {
        const style = {}
        // ÁªßÁª≠ÊâßË°åÂä®Áîª
        style[
          'transform'
        ] = `translate3D(${this.temporaryData.lastPosWidth},${this.temporaryData.lastPosHeight}px,0px)`
        style['opacity'] = '0'
        style['zIndex'] = '-1'
        style['transitionTimingFunction'] = 'ease'
        style['transitionDuration'] = 300 + 'ms'
        return style
      }
    },
    // È¶ñÈ°µÊ†∑ÂºèÂàáÊç¢
    transformIndex(index) {
      // Â§ÑÁêÜ3DÊïàÊûú
      if (index === this.basicdata.currentPage) {
        const style = {}
        style[
          'transform'
        ] = `translate3D(${this.temporaryData.posWidth}px,${this.temporaryData.posHeight}px,0px)`
        style['opacity'] = this.temporaryData.opacity
        style['zIndex'] = 10
        if (this.temporaryData.animation) {
          style['transitionTimingFunction'] = 'ease'
          style['transitionDuration'] = 300 + 'ms'
        }
        return style
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.stack {
    width: 320px;
    height: 320px;
    position: relative;
    perspective: 1000px;
    perspective-origin: 50% 150%;
    -webkit-perspective: 1000px;
    -webkit-perspective-origin: 50% 150%;
    margin: 0;
    padding: 0;
}
.stack-item {
    background: #fff;
    height: 100%;
    width: 100%;
    border-radius: 4px;
    text-align: center;
    overflow: hidden;
    position: absolute;
    opacity: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
    flex-direction: column;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    pointer-events: auto;
}
.stack-item img {
    width: 100%;
    display: block;
    pointer-events: none;
}
.stack-container li.move-back {
    /* http://matthewlein.com/ceaser/ */
    -webkit-transition-timing-function: cubic-bezier(
        0.175,
        0.885,
        0.47,
        1
    ); /* older webkit */
    -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.47, 1.515);
    transition-timing-function: cubic-bezier(0.175, 0.885, 0.47, 1.515);
}
</style>
